'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name nditerColumns
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/iter/columns}
*/
import nditerColumns from '@stdlib/ndarray-iter-columns';
setReadOnly( ns, 'nditerColumns', nditerColumns );

/**
* @name nditerRows
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/iter/rows}
*/
import nditerRows from '@stdlib/ndarray-iter-rows';
setReadOnly( ns, 'nditerRows', nditerRows );

/**
* @name nditer2arrayEach
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/ndarray/iter/to-array-each}
*/
import nditer2arrayEach from '@stdlib/ndarray-iter-to-array-each';
setReadOnly( ns, 'nditer2arrayEach', nditer2arrayEach );


// EXPORTS //

export default ns;
